<template>
  <div class="oraderList">
    <h2></h2>
    <div style="margin-bottom:20px">
      <span></span>
      <selection :selectParam="selectParam" @select-val="onSelectBox"></selection>
      <span style="margin-left:15px"></span>
      <el-date-picker
        v-model="value1"
        type="date"
        placeholder=""
        size="small"
      >
      </el-date-picker>
      <span style="margin-left:15px"></span>
      <el-date-picker
        v-model="value2"
        type="date"
        placeholder=""
        size="small"
      >
      </el-date-picker>
      <span style="margin-left:15px"></span>
      <input class="keyword" type="text"/>
    </div>
    <div style="padding-bottom:200px">
      <table>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
        <tr>
          <td>ddj123</td>
          <td></td>
          <td></td>
          <td>1</td>
          <td>2016-10-10</td>
          <td>2</td>
          <td>500</td>
        </tr>
        <tr>
          <td>yuj583</td>
          <td></td>
          <td></td>
          <td>3</td>
          <td>2016-5-2</td>
          <td>1</td>
          <td>2200</td>
        </tr>
        <tr>
          <td>pmd201</td>
          <td></td>
          <td></td>
          <td>3</td>
          <td>2016-8-3</td>
          <td>12</td>
          <td>7890</td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
import selection from '../components/selection'
export default {
  data: function(){
    return {
      selectParam: ['','','',''],
      gridData: [
        {
          type: '',
          address:'',
          time: '',
          price: '500'
        }
      ],
      value1: '',
      value2: ''
    }
  },
  components: {
    selection
  },
  methods: {
    onSelectBox: function(pra){
      this.gridData[0].address = pra;
    },
  }
}
</script>

<style scoped>
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/ 
.oraderList h2 {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 20px;
}

.keyword {
  width: 200px;
  height: 32px;
  line-height: 30px;
  padding-left: 10px;
  border:1px solid #ccc;
}

table {
  width: 100%;
}

table th {
  background-color: #4fc08d;
  color:#fff;
  height: 30px;
  border:1px solid #4fc08d;
}

table td {
  height: 40px;
  line-height: 40px;
  text-align: center;
  border:1px solid #ccc;
}
</style>



