<template>
  <div class="app-head clearfix">
    <div class="app-head-inner pull-left">
      <router-link to="/" class="head-logo">
        <img src="../assets/logo.png">
      </router-link> 
    </div>
    <ul class="pull-right clearfix">
      <li @click="isLayout=true;isShadow=true;state='login'"></li>
      <li>|</li>
      <li @click="isLayout=true;isShadow=true;state='register'"></li>
      <li>|</li>
      <li @click="isLayout=true;isShadow=true;state='about'"></li>
    </ul>

    <mask-layer v-show="isShadow" @click.native="isShadow = false;isLayout=false"></mask-layer>

    <layout :isLayout="isLayout" :state="state" @close-layout="onCloseLayout">
      <div slot="login" class="login">
        <div>
          <label></label>
          <input type="text" placeholder="" />
        </div>
        <div>
          <label></label>
          <input type="text" placeholder=""/>
        </div>
        <div>
          <label>&nbsp;</label>
          <button> </button>
        </div>
      </div>
      <div slot="register" class="register login">
        <div>
          <label></label>
          <input type="text" placeholder="" />
        </div>
        <div>
          <label></label>
          <input type="text" placeholder=""/>
        </div>
        <div>
          <label>&nbsp;</label>
          <button> </button>
        </div>
      </div>
      <div slot="about" class="about">
        <p></p>
      </div>
    </layout>
  </div>
</template>

<script>
import layout from '../components/layout'
import maskLayer from '../components/maskLayer'
export default {
  data: function () {
    return {
      isShadow: false,
      isLayout: false,
      state: ''
    }
  },
  components: {
    layout,
    maskLayer
  },
  methods: {
    onCloseLayout: function(pra){
      this.isLayout = pra;
      this.isShadow = pra;
    }
  }
}
</script>

<style scoped>
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

.app-head {
  width: 1200px;
  height: 90px;
  margin:0 auto;
  line-height: 90px;
}

.app-head ul li {
  float: left;
  color:#b2b2b2;
  margin-left: 10px;
  cursor: pointer;
}

.app-head-inner img{
  vertical-align: middle;
  width: 55px;
}

.el-dialog__body {
  padding-bottom: 0;
}

.login div+div {
  margin-top: 25px;
}

.login label {
  display: inline-block;
  width: 80px;
  text-align: left;
}

.login input {
  width: 300px;
  height: 34px;
  border:1px solid #ccc;
  padding-left: 10px;
}

.login button {
  width: 70px;
  height: 42px;
  line-height: 42px;
  background: #4fc08d;
  color:#fff;
  font-size: 16px;
}

.about p {
  line-height: 1.6;
  text-indent: 2em;
}
</style>



