<template>
  <div>
    <div class="detail-box" style="padding-bottom:40px;">
      <h2></h2>
      <h3></h3>
      <div class="detail-box-left">
        <span></span>
        <checkbox :checkboxParam="checkboxParam" @checkbox-val="onCheckBoxVal"></checkbox>
      </div>
      <div class="detail-box-left">
        <span></span>
        <selection :selectParam="selectParam" @select-val="onSelectBox"></selection>
      </div>
      <div class="detail-box-left">
        <span></span> 
      </div>
      <div class="detail-box-left">
        <span></span> 500
      </div>
      <div class="detail-box-left">
        <span>&nbsp;</span> <button @click="isLayout=true;isShadow=true;state='count'"></button>
      </div>
    </div>

    <mask-layer v-show="isShadow" @click.native="isShadow = false;isLayout=false;isCheck=false;isSuccess=false;isFail=false"></mask-layer>
    
    <layout :isLayout="isLayout" :state="state" @close-layout="onCloseLayout">
      <div slot="count" class="count-layer">
        <table>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
          <tr>
            <td>{{gridData[0].type}}</td>
            <td>{{gridData[0].address}}</td>
            <td>{{gridData[0].time}}</td>
            <td>{{gridData[0].price}}</td>
          </tr>
        </table>
        <button @click="isLayout=false;state='check';isCheck=true"></button>
      </div>
    </layout>

    <layout :isLayout="isCheck" :state="state" @close-layout="onCheckLayout">
      <div slot="check" class="check-layer count-layer">
        <p></p>
        <button @click="goRoute"></button>
        <button @click="isCheck=false;state='fail';isFail=true"></button>
      </div>
    </layout>

    <layout :isLayout="isSuccess" :state="state" @close-layout="onSuccessLayout">
      <div slot="success">
        <p>{{num}}s...</p>
      </div>
    </layout>

    <layout :isLayout="isFail" :state="state" @close-layout="onFailLayout">
      <div slot="fail">
        <p></p>
      </div>
    </layout>

    <div class="detail-box" style="padding-bottom:20px">
      <h2></h2>
      <h3></h3>
      <table>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
import checkbox from '../components/checkbox'
import selection from '../components/selection'
import layout from '../components/layout'
import maskLayer from '../components/maskLayer'
export default {
  data: function () {
    return {
      checkboxParam: [
        {
          title: '',
          id: 'red',
          type: 'radio',
          name: 'radio-box'
        },
        {
          title: '',
          id: 'green',
          type: 'radio',
          name: 'radio-box'
        },
        {
          title: '',
          id: 'violet',
          type: 'radio',
          name: 'radio-box'
        }
      ],
      selectParam: ['','','','','',''],
      product:'',
      gridData: [
        {
          type: '',
          address:'',
          time: '',
          price: '500'
        }
      ],
      isShadow: false,
      isLayout: false,
      isCheck: false,
      isSuccess: false,
      isFail: false,
      state: '',
      num: 5
    }
  },
  components: {
    checkbox,
    selection,
    layout,
    maskLayer
  },
  created: function(){
    this.menu();
  },
  methods: {
    menu: function(){
      window.scrollTo(0,0);
    },
    onCheckBoxVal: function(pra){
      this.gridData[0].type = pra.join(",");
    },
    onSelectBox: function(pra){
      this.gridData[0].address = pra;
    },
    onCloseLayout: function(pra){
      this.isLayout = pra;
      this.isShadow = pra;
    },
    onCheckLayout: function(pra){
      this.isCheck = pra;
      this.isShadow = pra;
    },
    onSuccessLayout: function(pra){
      this.isSuccess = pra;
      this.isShadow = pra;
    },
    onFailLayout: function(pra){
      this.isFail = pra;
      this.isShadow = pra;
    },
    goRoute: function(){
      let self = this;
      let time = null;
      this.isCheck=false;
      this.state='success';
      this.isSuccess=true;
      time = setInterval(function(){
        if(self.num <= 0){
          clearInterval(time);
          self.$router.push('/oraderList');
        }else {
          self.num --;
        }
      },1000);
    }
  }
}
</script>

<style scoped>
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
.detail-box table th {
  padding-left: 15px;
  border:1px solid #f0f2f5;
  height: 46px;
  line-height: 44px;
}
</style>



